import React from 'react';
import './style.css';

function NavBar() {
  return (
    <div className="navbar">
      <img
        className="logo"
        src=" https://upload.wikimedia.org/wikipedia/commons/thumb/0/08/Netflix_2015_logo.svg/1920px-Netflix_2015_logo.svg.png"
        alt="Netflix_2015_logo"
      />
    </div>
  );
}
export default NavBar;
